'use client';
import React, { useState } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/router';
import { FiMenu, FiX } from 'react-icons/fi';

const Navbar: React.FC = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const router = useRouter();

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  const navLinks = [
    { href: '/', label: 'Home' },
    { href: '/dermats', label: 'Dermats' },
    { href: '/magic-code', label: 'Magic Code' },
    { href: '/about-us', label: 'About Us' },
    { href: '/blogs', label: 'Blogs' },
  ];

  return (
    <nav className="w-full fixed top-4 left-1/2 transform -translate-x-1/2 flex justify-between items-center bg-[#1C1B1FCC] px-4 z-50 rounded-[16px] shadow-2xl backdrop-blur-lg">
      {/* Logo */}
      <div className="w-[114px] h-[40px]">
        <img
          src="/images/logo.svg"
          alt="Logo"
          className="w-full h-full object-contain"
        />
      </div>

      {/* Hamburger Icon for Mobile */}
      <div
        className="md:hidden text-white text-3xl cursor-pointer"
        onClick={toggleMenu}
      >
        {menuOpen ? <FiX /> : <FiMenu />}
      </div>

      {/* Navigation Links */}
      <div
        className={`md:flex md:items-center md:gap-4 ${
          menuOpen
            ? 'flex flex-col absolute top-16 left-0 w-full bg-[#1C1B1FCC] p-4'
            : 'hidden'
        } md:static md:bg-transparent`}
      >
        {navLinks.map((link) => (
          <Link key={link.href} href={link.href}>
            <span
              className={`text-[20px] font-medium cursor-pointer text-white px-3 py-2 rounded-lg transition-all duration-300 ${
                router.pathname === link.href
                  ? 'bg-white text-[#1C1B1F]'
                  : 'hover:bg-white hover:text-[#1C1B1F]'
              }`}
            >
              {link.label}
            </span>
          </Link>
        ))}
      </div>
    </nav>
  );
};

export default Navbar;
